@font-face {
  font-family: Poppins;
  src: url(./assets/fonts/Poppins-Regular.ttf);
}

@font-face {
  font-family: Poppins-Black;
  src: url(./assets/fonts/Poppins-ExtraBold.ttf);
}

@font-face {
  font-family: Poppins-semibold;
  src: url(./assets/fonts/Poppins-SemiBold.ttf);
}

@font-face {
  font-family: Poppins-regular;
  src: url(./assets/fonts/Poppins-Regular.ttf);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  background-color: var(--primary-color);
  font-family: "Poppins", sans-serif;
  cursor: default;
}

:root {
  /* Colors */
  --primary-color: rgb(249, 249, 249);
  --secondary-color: rgb(0, 128, 128);
  --tertiary-color: rgb(255, 51, 149);
  --text-color-black: rgb(255, 240, 247);
  --text-color-dark: rgb(255, 240, 247);

  /* TYPOGRAPHY */
  --biggest-fontsize: 4rem;
  --big-fontsize: 3rem;
  --title-font-size: 2rem;
  --subtitle-font-size: 1.5rem;
  --text-font-size: 1rem;
  --small-fontsize: 0.5rem;
  --smaller-fontsize: 0.3rem;

  /*  */
  --weight-bold: 700;
  --weight-semi-bold: 600;
  --weight-normal: 500;
  --weight-extra-light: 400;
}

/* REUSABLE VALUES */
h1,
h2,
h3,
h4,
h5,
p,
ul,
li,
a,
div,
article,
img,
address,
input,
textarea,
header,
nav {
  background-color: transparent;
}

a {
  cursor: pointer;
  text-decoration: none;
}

h1,
h2 {
  font-size: var(--title-font-size);
  font-weight: var(--weight-bold);
  color: var(--text-color-black);
  font-family: "Poppins-Black", sans-serif;
}

h3,
h4 {
  font-size: var(--text-font-size);
  font-weight: var(--weight-normal);
  color: var(--text-color-dark);
}

p {
  color: var(--text-color-dark);
  font-size: var(--text-font-size);
}

.content {
  max-width: 100vw;
  min-height: 100vh;
}

.icon {
  width: 25px;
  aspect-ratio: 1;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.grid {
  display: grid;
  place-content: center;
}

.card {
  width: 40vw;
  height: 60vh;
  box-shadow: 2px 4px 6px var(--text-color-dark);
  border-radius: 2rem;
  padding: 20px;
}

.section,
.content {
  margin-top: 70px;
}

.navbar {
  width: 100vw;
  display: grid;
  place-items: center;
  margin-inline: auto;
  position: fixed;
  top: 0;
  min-height: 70px;
  z-index: 100;
  box-shadow: 2px 3px 5px var(--text-color-black);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(20px);
  background-color: rgba(0, 128, 128, 0.5);
}

.new-header {
  background-color: var(--tertiary-color);
}

.nav_container {
  width: 100%;
  height: fit-content;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: all 0.4s ease;
}

.nav_logo {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.logo_img {
  padding: 5px;
  background-color: white;
  aspect-ratio: 1;
  border-radius: 50%;
  display: grid;
  place-items: center;
}

.logo_img img {
  width: 40px;
}

.nav_logo h2 {
  color: var(--primary-color);
  font-size: 1.5rem;
}

.links,
.socials {
  display: flex;
  align-items: center;
  gap: 3rem;
  list-style: none;
}

.list_item-link {
  color: var(--primary-color);
  text-decoration: none;
}

.menu_icon {
  display: none;
  background-color: var(--secondary-color);
}

.closed {
  left: -100%;
}

a {
  transition: all 0.4s ease;
}

a:hover {
  color: yellowgreen;
}

.navlogo2 {
  align-self: center;
  justify-self: start;
  color: var(--primary-color);
  display: none;
}

/* =======================    HOME ========================= */
.content {
  width: 100vw;
  overflow-x: hidden;
}

.home_content {
  display: grid;
  place-items: center;
  width: 100%;
  background-image: url("./assets/images/happy-kids.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
}

.dim {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background: linear-gradient(
    45deg,
    rgba(0, 0, 0, 0.75) 30%,
    rgba(0, 0, 0, 0.5) 70%
  );
  z-index: 1;
}

.home_data_container {
  width: 80%;
  display: grid;
  place-items: center;
  z-index: 2;
}

.home_data {
  align-items: center;
  padding: 20px;
}

.home_title {
  font-size: var(--biggest-fontsize);
  color: var(--secondary-color);
}

.home_perfil {
  width: 100%;
  aspect-ratio: 1;
  background-color: var(--secondary-color);
  display: none;
  place-items: center;
  overflow: hidden;
  border-radius: 8px;
}

.perfil_image {
  width: 100%;
  height: 100%;
}

@media screen and (min-width: 716px) {
  .btn:hover {
    background-color: transparent;
  }
}

@media screen and (max-width: 716px) {
  :root {
    --text-color-black: rgb(115, 115, 115);
    --text-color-dark: rgb(87, 87, 87);
  }

  .btn:hover {
    background-color: transparent;
  }

  .home_data_container {
    width: 100%;
  }
  .home_perfil {
    display: grid;
  }

  .home_content {
    background-image: none;
    background-color: var(--primary-color);
  }

  .dim {
    display: none;
  }
}

.home_buttons {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 6rem;
  z-index: 2;
}

.btn {
  padding: 10px;
  width: 300px;
  font-size: var(--text-font-size);
  border-radius: 8px;
  border: 2px solid transparent;
  color: var(--primary-color);
  background-color: var(--secondary-color);
  cursor: pointer;
  transition: all 0.4s ease;
}

.btn:hover {
  background-color: var(--primary-color);
  color: var(--secondary-color);
  border: 2px solid var(--secondary-color);
}

/* =======================    ACHIEVEMENTS ========================= */
.achievements_grid {
  padding: 20px;
  display: grid;
  place-items: center;
}

.achievements_description {
  width: 80vw;
  text-align: center;
  margin-bottom: 30px;
  border: none;
}

.achievements_description hr {
  height: 2px;
  margin-top: 20px;
}

.container_grid {
  display: grid;
  place-content: center;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.contact {
  flex-direction: column;
  width: 100%;
}

.contact_form {
  width: 90%;
  height: 60px;
  background-color: var(--secondary-color);
  display: flex;
  flex-direction: column;
  margin: 10px;
  align-items: left;
  border-radius: 8px;
  justify-content: center;
  color: White;
  text-align: left;
}

.contact_form p {
  color: white;
  a {
    color: var(--tertiary-color);
  }
}

.achievement_card {
  box-shadow: 2px 3px 6px var(--text-color-black);
  display: grid;
  position: relative;
  place-content: center;
  overflow: hidden;
  border-radius: 1rem;
  height: 220px;
  cursor: pointer;
}

.achievement_card_img {
  aspect-ratio: 16/9;
  height: 220px;
  transition: all 0.4s ease;
}

.achievement_card:hover .achievement_card_img {
  transform: scale(1.2);
}

.achievement_card_info {
  position: absolute;
  bottom: -100%;
  left: 0;
  background-color: rgba(0, 128, 128, 0.5);
  width: 100%;
  height: 100%;
  padding: 20px;
  z-index: 1;
  transition: all 0.4s ease;
  display: flex;
  align-items: center;
}

.achievement_card_info p {
  color: var(--primary-color);
}

.achievement_card:hover .achievement_card_info {
  bottom: 0;
}

.joinbutton {
  text-decoration: none;
  text-align: center;
  padding: 15px 30px;
  margin-top: 80px;
}

/* EXTRAS */

/* =======================    HOME ========================= */
/* =======================    HOME ========================= */

/* FOOTER */
footer {
  width: 100vw;
  background-color: var(--secondary-color);
  color: var(--primary-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: fit-content;
  margin-top: 0px;
}

footer h1 {
  color: var(--primary-color);
}

.footer_links {
  display: flex;
  align-items: center;
  gap: 3rem;
  list-style: none;
}

.footer_links li,
.list_item {
  width: 200px;
  padding: 10px;
  border-radius: 8px;
  text-align: center;
  transition: all 0.4s ease;
}

.list_item {
  width: fit-content;
}

.footer_links li:hover,
.list_item:hover {
  background-color: rgba(255, 255, 255, 1);
  color: var(--secondary-color);
}

.footer_links li:hover .footer_link,
.list_item:hover .list_item-link {
  color: var(--secondary-color);
}

.footer_link {
  color: var(--primary-color);
  text-decoration: none;
}

.footer_copy {
  width: 90%;
  margin: 20px;
  border-top: 2px solid var(--primary-color);
  text-align: center;
  color: var(--primary-color);
}

.footer_copy p {
  color: var(--primary-color);
}

/* about section */

.about-container {
  width: 80vw;
  height: 100%;
  display: grid;
  grid-template-rows: max-content;
  gap: 3rem;
  padding: 10px;
}

.content_pars {
  text-align: left;
}

/*=============== SCROLL BAR ===============*/
::-webkit-scrollbar {
  width: 0.2rem;
  border-radius: 0.5rem;
  background-color: hsla(0, 0%, 10%, 0.1);
}

::-webkit-scrollbar-thumb {
  border-radius: 0.5rem;
  background-color: hsl(0, 0%, 20%);
}

::-webkit-scrollbar-thumb:hover {
  background-color: hsl(0, 0%, 30%);
}

/* ====================== BREAK POINTS ========================== */
@media screen and (max-width: 978px) {
  :root {
    --biggest-fontsize: 2.5rem;
    --big-fontsize: 2rem;
    --title-font-size: 1.8rem;
    --subtitle-font-size: 1.5rem;
    --text-font-size: 1rem;
    --small-fontsize: 0.5rem;
    --smaller-fontsize: 0.3rem;
  }

  .navbar {
    top: 0;
    left: 0;
    position: fixed;
  }

  .menu_icon {
    display: block;
    position: fixed;
    top: 1.5rem;
    right: 1.5rem;
    background-color: transparent;
  }

  .nav_container {
    position: fixed;
    top: 0;
    width: 100vw;
    height: 100vh;
    transition: all 0.4s ease;
    background-color: var(--secondary-color);
    flex-direction: column;
    align-items: flex-start;
  }

  .links {
    flex-direction: column;
    font-size: calc(var(--text-font-size) + 4px);
    align-self: center;
  }

  .navlogo2 {
    display: block;
  }

  .navlogo2 {
    color: var(--secondary-color);
  }

  .nav_logo h2 {
    display: none;
  }

  .nav_logo {
    align-self: center;
  }

  .logo_img {
    width: 80px;
    img {
      width: 60px;
    }
  }

  .socials {
    align-self: flex-end;
  }

  .footer_links {
    flex-direction: column;
  }

  .card {
    width: 95%;
  }

  .content {
    padding: 3px;
  }

  .home_data_container {
    display: flex;
    flex-direction: column;
  }

  .home_data {
    padding: 5px;
    order: 2;
  }

  .home_title {
    font-size: var(--big-fontsize);
    font-weight: var(--weight-normal);
  }

  .home_perfil {
    width: 80%;
    aspect-ratio: 16/9;
    order: 1;
  }

  .home_buttons {
    flex-direction: column;
    gap: 1.5rem;
  }

  .achievements_grid {
    padding: 5px;
  }

  .achievements_description {
    text-align: start;
  }

  .container_grid {
    grid-template-columns: max-content;
  }

  .achievement_card_img {
    width: 320px;
  }
}

.footer_socials {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  margin: 12px;
}

.footer_socials a {
  cursor: pointer;
  transition: all 0.4s ease;
}

.footer_socials a:hover {
  transform: translateY(-4px);
}
